.aboutus_cntnr{

    background-color: #9B8BFF;
    height: auto;

}

.aboutus_main_hr1 , .aboutus_main_hr2 , .aboutus_main_hr3{

    width: 75%;
    height: 5px;

    border-radius: 3px;
    border: none;

    background-color: #9B8BFF;

    margin-top: 40px;
}


/*        MAIN          */

.aboutus_main{

    width: 100%;
    /* height: 1500px; */

    border-top: 6px solid #F2FF59;
    border-radius: 50px 50px 0 0;

    background-color: white;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
}

/*     Rate Function     */

.rateFunction{

    
    width: 80%;

    display: flex;
    flex-direction: column;
    
    
    gap: 40px;

}

.rateFunction_h2{

    text-decoration: underline #9B8BFF;
    text-decoration-thickness: 2px;
    text-underline-offset: 5px;

    font-family: 'Arvo';

}

.rateFunction_div{

    display: flex;
    align-items: center;
    justify-content: space-around;
}

.rateFunction_textarea{

    width: 40%;
    height: 200px;

    border: 2px solid;
    border-radius: 15px;

    padding: 40px;

    font-family: 'Arvo';
    font-style: italic;

    line-height: 1.5;

    background-color: #ddd;
    color: black;

    resize: none;

    /* margin-left: 100px; */
}

.rateFunction_textarea::placeholder{
    color: #666;
}

.rateFunction_textarea:hover{
    cursor: pointer;
    background-color: #eee;
}

.rateFunction_textarea:focus{
    cursor: text;
}


.rateFunction_starsRating{

    display: flex;
    flex-direction: column;

}

.rateFunction_starsRating_p{

    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;

    font-family: 'Arvo';
    font-weight: 600;

}

.rateFunction_starsRating_div{

}


/*     Our Team       */

.ourTeam{

    width: 80%;
    /* height: 2000px; */
    display: flex;
    flex-direction: column;
    gap: 20px;

    margin-top: 30px;

}

.ourTeam_h2{

    text-decoration: underline #9B8BFF;
    text-underline-offset: 5px;
    text-decoration-thickness: 2px;


    font-family: 'Arvo';
}

.ourTeam_members{

    width: 100%;
    /* height: 2000px; */
    
    /* float: left; */
    display: flex;
    gap: 40px;
    flex-wrap: wrap;
}


/*     Frameworks     */

.frameworks{

    
    width: 80%;

    display: flex;
    flex-direction: column;
    gap: 40px;

    margin-top: 40px;

}

.frameworks_content{
    margin-left: 60px;
    display: flex;
    flex-direction: column;
    gap: 50px;
}

.frameworks_h2{

    text-decoration: underline;
    text-decoration-color: #9B8BFF;
    text-underline-offset: 5px;

    font-family: 'Arvo';

}

        /*   React   */

        .framework_react{

            display: flex;
            justify-content: flex-start;
            gap: 20px;

        }

        .reactDescription{

            display: flex;
            flex-direction: column;

            width: 67%;

            font-family: 'Arvo';


        }

        .reactImg{

            width: 200px;

            border-radius: 20px;

        }

    
        .reactDescription_h3{

        }

        .reactDescription_p{

            font-style: italic;

        }



        /*   Django   */

        .framework_django{

            display: flex;
            justify-content: flex-start;
            gap: 20px;

        }

        .djangoImg{
            width: 200px;

        }

        .djangoDescription{

            font-family: 'Arvo';

        }

        .djangoDescription_h3{

        }

        .djangoDescription_p{

            font-style: italic;

        }


/*            FOOTER          */

.aboutus_footer{

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

    padding-top: 40px;

    background-color: white;
    

}

.aboutus_footer div{
    
    font-family: 'Arvo';
    font-style: italic;
    font-weight: 600;
    font-size: 30px;

}


.aboutus_footer_div1 label , .aboutus_footer_div2 label{

    font-family: 'Arvo';
    font-weight: 600;
    font-size: 30px;
}


.aboutus_footer_div1 ul , .aboutus_footer_div2 ul{

    list-style-type: none;

    display: flex;
    justify-content: space-between;

}

.aboutus_footer_div1 ul li , .aboutus_footer_div2 ul li{
    cursor: pointer;
}

