.Home-main{

    height: 100vh;

    position: relative;

    border-radius: 50px 50px 0 0;
    border-top: 6px #F2FF59 solid;
    border-bottom: 3px solid black;

    background-color: #FBFBFB;
}

.Home-header{

    position: absolute;
    top: 0px;
    left: 50%;
    transform: translateX(-50%);

    height: 7vh;
    width: 25vw;

    border-radius: 0 0 20px 20px;

    background-color: #F2FF59;

    text-align: center;

    font-size: 1.5vw;
    
}