.Statistics-cntnr{

    width: 90%;
    height: 400px;

    margin-top: 100px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    /* align-items: center; */
    gap: 40px;

    border-radius: 20px;
    border-top: 5px solid #9B8BFF;
}

.statistics_h2{
    font-style: italic;
    display: flex;
    justify-content: center;
    align-items: center;

}

.statistics_main{

    display: flex;
    justify-content: space-evenly;
    align-items: center;
    flex-wrap: wrap;
    gap: 10px;

}

.statistics_Box{

    width: 22vw;
    height: 25vh;

    border: none;
    /* border-radius: 30px; */

    background-color: white;

    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;

    

    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.25),
                0px -6px 20px rgba(0, 0, 0, 0.25), 
                6px 0px 20px rgba(0, 0, 0, 0.25), 
                -6px 0px 20px rgba(0, 0, 0, 0.25);

    position: relative;

    
    position: relative;

    overflow: hidden;

}

.statistics_1stBox::before{

    content: '';
    position: absolute;
    top: -30px;
    left: -30px;

    width: 130px;
    height: 130px;

    border-radius: 50%;

    background-color: #4ED837;

}

.statistics_2ndBox::before{

    content: '';
    position: absolute;
    top: -30px;
    left: -30px;

    width: 130px;
    height: 130px;

    border-radius: 50%;

    background-color: #FF4E4E;

}

.statistics_3rdBox::before{

    content: '';
    position: absolute;
    top: -30px;
    left: -30px;

    width: 130px;
    height: 130px;

    border-radius: 50%;

    background-color: #524EFF;

}

.Box_div{
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;

    margin-right: 30px;

}

.uniFontP{
 
    font-family: 'Bungee', cursive;
    font-size: 18px;
}

.uniHeading{
    font-style: italic;
    color: rgb(72, 119, 119);

    display: flex;
    justify-content: center;
    align-items: center;

    margin-right: 50px;

}