.my_nav{

    margin: 0;

    background-color: #9B8BFF;

    list-style: none;

    display: flex;
    justify-content: space-around;
    align-items: center;

    height: 140px;

}

.my_nav li{

    cursor: pointer;

    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;

}

.my_nav li:last-child{

    display: flex;
    align-items: center;
    justify-content: space-around;

    color: azure;

    font-family: 'Arvo';
    font-style: italic;

    width: 10%;
}

.my_nav_point{
    position: absolute;
    width: 10px;
    height: 10px;

    border-radius: 50%;

    background-color: white;

    margin-top: 20px;

    /* display: none; */
    
}

.my_nav li a.active{


    /* background-color: black; */
    transform: translateY(-30px);
    
}