.LandingFooter-cntnr{

    background: linear-gradient(87.48deg, #9B8BFF 0.42%, #82A7FF 57.05%, #00BDFF 93.58%);

    display: flex;
    justify-content: space-between;
    align-items: center;

    border-radius: 50px 50px 0 0;

    height: 300px;
    width:100%;

    margin-top: 100px;


}

/*  */

.LandingPage-cntnr_section1{

    padding-left: 40px;

    display: flex;
    align-items: center;
    /* gap: 40px; */

}

.LandingPage-cntnr_section1_img{

    width: 200px;
    height: 80px;

}

.LandingPage-cntnr_section1_div{

    color: white;

    line-height: 28px;
}

.LandingPage-cntnr_section1_div p:nth-child(2){
    padding-left: 20px
}

.LandingPage-cntnr_section1_div p:nth-child(3){
    padding-left: 40px
}

.LandingPage-cntnr_section1_div p:nth-child(4){
    padding-left: 20px
}

/*  */

.LandingPage-cntnr_section2{
    margin-right: 200px;
}


.LandingPage-cntnr_section2_h3{
    color: aliceblue;

    text-decoration: underline;

}

.LandingPage-cntnr_section2_table td{

    cursor: pointer;

}

.LandingPage-cntnr_section2_table td:nth-child(2){
    padding-left: 20px;
}