.card_cntnr{

    width: 340px;
    height: 420px;

    
    border-radius: 25px;

    position: relative;

    background-color: #E6E6E6;

    margin-left: 20px;

    /* display: block; */

}

.card_cntnr:hover{
    cursor: pointer;
    box-shadow: 0px 0px 40px 15px rgba(0, 0, 0, 0.25);
    transform: translateY(-10px);

    transition: 0.8s;
}

/*               Background             */

.card_background{

    width: 100%;
    height: 130px;

    background-color: #9B8BFF;

    border-radius: 25px;

}

/*              Content                 */

.card_content{

    width: 100%;
    
    position: absolute;
    top: 0;

    display: flex;
    flex-direction: column;
}

        /*        Head         */

        .card_head{

            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;

            width: 100%;
            height: auto;
        }

        .card_head_h2{

            font-family: 'Arvo';
            color: white;

            text-align: center;
            
        }

        .card_head_pic{

            width: 100px;
            height: 100px;

            border-radius: 50%;
        }

        .card_head_h3{

            font-family: 'Arvo';
            font-weight: 400;

        }

        /*        Body         */

        .card_body{

            width: 100%;
            height: auto;
             
        }

        .card_body_table{
            width: 100%;

            font-family: 'Arvo' ;
            font-size: 15px;
            
        }

        .card_body_table td{
            padding-left: 25px;
            text-align: left;
            /* overflow-x: hidden; */
        }

        .card_body_table td:nth-child(1){
            vertical-align: middle;
            text-align: center;

            transform: translateY(3px);
        }

        .card_body_table tr{
            height: 33px;
        }

