.Header-cntnr{

    
    background: linear-gradient(88.13deg, #9B8BFF 13.08%, #73B6FF 54.27%, #00C2FF 100%);
    width: 100%;
    height: 40vh;

    border-radius: 100% 0% 100% 0% / 0% 32% 68% 100% ;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    
    
   
}
/*  */

.Header_navbar{
    
    display: flex;
    align-items: center;
    /* justify-content: ; */
    gap: 200px;
    flex-wrap: wrap;

    padding-top: 20px;
    margin-left: 40px;
}

.Header-navbar-section1{

    display: flex;
    gap: 30px;

}

.header_nav_link{
    text-decoration: none;

    font-family: 'Arvo';
    font-style: italic;
    font-weight: 500;
    font-size: 1.4vw;
    
}


.header_left_section{

    color: white;

    position: relative;
}

.header_left_section:after{
    
    content: '';
    width: 0;
    height: 2px;
    background-color: white;

    position: absolute;
    bottom: -3px;
    left: 0;
    
}

.header_left_section:hover:after{
    width: 100%;
    transition: 300ms;
}

.Header-navbar-section2{
    display: flex;
    gap: 30px;
}

.header_right_section{

    color: black;

    position: relative;
}

.header_right_section:after{
    
    content: '';
    width: 0;
    height: 2px;
    background-color: black;

    position: absolute;
    bottom: -3px;
    left: 0;
    
}

.header_right_section:hover:after{
    width: 100%;
    transition: 300ms;
}


/*  */

.Header_belowNav{
    
    display: flex;
    justify-content: space-around;
    align-items: center;
}

.Header_belowNav_p{
    width: 25vw;
    font-size: 1.4vw;

    text-align: center;

    color: white;

    font-style: italic;
}

.mySpan{
    color: #FE3948;
}

.busyEmployee-img{
    width: 18em;
    transform: translate(40px,0);
}