.Ranking_container{

    background-color: #9B8BFF;
    
    width: 100%;
    height: 1400px;

    display: flex;
    flex-direction: column;
    /* justify-content: center; */

    font-family: 'Arvo';
    
}

/*    NAV    */

.Ranking_navbar{

    height: 8%;

    list-style-type: none;

    display: flex;
    justify-content: space-around;
    align-items: center;

}

.Ranking_navbar li{

    cursor: pointer;
}

.Ranking_navbar li:last-child{

    width: 120px;

    display: flex;
    align-items: center;
    justify-content: space-between;

    font-weight: 600;
    font-style: italic;

    color: white;


}



/*    Main    */
.Ranking_main{

    height: 80%;

    border-top: 6px solid #F2FF59;
    border-radius: 50px 50px 0 0;

    background-color: azure;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    position: relative;

}

/*     DIVs    */


/*     DIV1    */

.Ranking_main_div1{

    display: flex;
    justify-content: space-around;
    align-items: center;

    height: 25%;
    width: 100%;

    z-index: 1;

    /* padding-top: 60px; */

}

.Ranking_main_div1_div{

    display: flex;
    justify-content: space-evenly;

    width: 50%;
}

.Ranking_main_div1_div_img{

    /* width: 5%; */
    width: 170px;
    height: 170px;

}

.Ranking_main_div1_div_section{

    display: flex;
    flex-direction: column;

}


.Ranking_main_div1_div_section_h3{

    font-style: italic;
    font-weight: 600;

    color: #9B8BFF;
}

.Ranking_main_div1_section2{
    
    font-weight: 700;
    font-style: italic;

    /* transform: translateX(130px); */
    width: 11%;
    
}

.Ranking_main_div2_section{

    display: flex;
    justify-content: flex-start;
    align-items: center;

    height: 80%;
    width: 80%;
}

.Ranking_main_div2_section_hr{

    width: 0.5%;
    height: 90%;

    background-color: #9B8BFF;
    border: none;
    border-radius: 2px;

    margin: 0px;
}

.Ranking_main_div2_section_div{

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    margin-left: 30px;

    width: 80%;
    height: 100%;
}

.Ranking_main_div2_section_div_section{
    display: flex;
    justify-content: space-around;
}

.Ranking_main_div2_section_div_section_div{

    border: 2px solid black;
    border-radius: 15px;

    display: flex;
    justify-content: space-around;
    align-items: center;

    width: 70%;

    font-family: 'Arvo';
    font-weight: 700;
    font-style: italic;
}

.Ranking_main_div2_ul{
    display: flex;
}



.Ranking_main_div1_section3{

    display: flex;
    /* justify-content: flex-start; */
    align-items: flex-start;

    /* border: 1px solid black; */

    
}

.Ranking_main_div1_section3_select{

    width: 150px;
    height: 40px;

    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;

    appearance: none;

    color: #C2BCBC;

    text-align: center;

    background-image: url('D:\My-Projects\React\function\public\Images\ArrowDown.svg');
    background-repeat: no-repeat;
    background-position: 5px;
    background-size: 14px;

    border: 2px solid black;

}

.Ranking_main_div1_section3_button{
    width: 50px;
    height: 40px;

    background-color: #9B8BFF;

    display: flex;
    align-items: center;
    justify-content: center;

    border-top-right-radius: 10px;
    border-bottom-right-radius: 10px;

    /* border: 1px solid black; */
    /* border: none; */

}

/*     DIV2    */

.Ranking_main_div2{

    width: 80%;
    height: 900px;

    border-radius: 20px;

    background: rgba(255, 255, 255, 0.62);

    box-shadow: 0px 4px 50px 9px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(25.5px);

    display: flex;
    flex-direction: column;
    align-items: center;

    z-index: 1;


}

.Ranking_main_div2_h3{

    color: #9B8BFF;

    font-style: italic;
}

/*      footer      */

.Ranking_footer{

    height: 10%;

    background-color: azure;

    /* display: flex; */

}

.Ranking_footer_div{

    display: flex;
    align-items: center;
    justify-content: space-around;

    font-family: 'Poppins';
    font-weight: 600;
    font-size: 16px;
    font-style: normal;

    padding-top: 20px;

}

.Ranking_footer_hr{

    margin-top: 20px;

    height: 5px;
    width: 96%;

    background-color: #9B8BFF;

    border: none;
    border-radius: 3px;
}

.Ranking_footer_div_a{

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 8%;

    text-decoration: none;
    color: black;

}

/*    Background  Circles    */

.Ranking_circle1{

    position: absolute;
    top: 300px;
    left: 75px;

    width: 300px;
    height: 300px;

    border-radius: 50%;

    background: rgba(115, 92, 255, 0.34);

    z-index: 0;

}

.Ranking_circle2{

    position: absolute;
    bottom: 20px;
    right: 75px;

    width: 300px;
    height: 300px;

    border-radius: 50%;

    background: rgba(115, 92, 255, 0.34);

    z-index: 0;


}