.sign_up{

    width: 100%;
    display: flex;

    margin: 20px 0;
}

/* @media and (max-width:900px){
    display: flex;
    flex-direction: column;

} */

.fingerprint_background{

    position: absolute;
    top: 56%;
    left: 42%;
    width: 30%;
    z-index: 0;
}

/* Left Section Style */

.sign_up_information{

    width: 56%;
    height: 1000px;
    border: 2px solid black;
    border-radius: 27px;
    margin : 20px 0px 0px 40px ;
    display: flex;
    flex-direction: column;
    justify-content: space-between;

    z-index: 1;

}

.section_flex{

    display: flex;
    flex-direction: column;
    /* justify-content: space-between; */

}

.div_flex{

    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

input{

    box-sizing: border-box;

    background: #F8F7F7;
    border: 2px solid #9B8BFF;
    border-radius: 11px;
    height: 40px;
    width: 100%;

    caret-color: #BD66FF;
    
    

    /* padding-left: 30px; */

}

input::placeholder{

    color: rgba(72, 72, 72, 0.4);
    padding-left: 15px;
    font-style: italic;
    font-weight: 700;
    font-size: 13px;
    line-height: 16px;

}

input[type='checkbox']{
    width: 20px;
    
}

.div2_section1 , .div2_section2 , .div2_section3{
    width: 25%;
}

.div3_section1{

    width: 50%;
    
}

.div3_section2{

    width: 35%;

}

.div4_section1{

    width: 25%;
}

.div4_section2{

    width: 50%;
    
}

.div5_section1{

    width: 25%;
    
}

.div5_section2{

    width: 50%;
    
}

.div6_section1{

    width: 40%;

}

.div8_section1{

    width: 60%;

}

.signup_div9_input1{
    
    width: 5%;
}

.signup_div9_label{

    font-family: 'Arvo';
    font-style: italic;
    font-weight: 700;
    font-size: 15px;
    line-height: 19px;
    width: 35%;
    margin-left: 8px;
}

.div1{

    margin-top: 20px;


    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.div2{
    
    /* margin-top: 30px; */

    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.div3{

    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.div4{

    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.div5{

    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.div6{

    display: flex;
    flex-direction: row;
    justify-content: flex-start;
    margin-left: 50px;
    

}

.div7{

    display: flex;
    flex-direction: row;
    justify-content: space-around;

}

.div8{

    display: flex;
    flex-direction: row;
    justify-content: center;

}

.div9{

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;

}

.div10{

    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;

}

.Security_Ques_Section{

    /* position: absolute; */

    font-style: italic;
    font-weight: 700;
    font-size: 21px;
    line-height: 26px;
    width: 40%;
    text-align: center;
    
    
}

.code{
    
    width: 30%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;


}

.hr1 , .hr2{

    border: 2px solid;
    width: 90%;
}

.signup_div9_link1 , .signup_div9_link2{

    text-decoration: none;
}

.btn_div{

    display: flex;
    justify-content: center;

    
    
}

.createAccountBtn{

    background: linear-gradient(90deg, #BC63FF -3.47%, rgba(188, 99, 255, 0.64) 103.32%);
    border-radius: 12px;

    color: white;

    width: 28%;
    height: 60px;

    cursor: pointer;

    display: flex;
    align-items: center;
    justify-content: center;

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
    border: #9B8BFF;
    

} 

.already_have_account{

    font-style: italic;
    font-weight: 700;
    font-size: 22px;
    line-height: 30px;

    width: 40%;
    display: flex;
    justify-content: center;

}

.login_link{

    color: #BD66FF;

    font-family: 'Arvo';
    font-style: italic;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;

    filter: drop-shadow(0px 6px 4px rgba(0, 0, 0, 0.25));

    width: 15%;
    display: flex;
    justify-content: center;

}

/* Right Section Style */



.sign_up_introduction{

    width: 35%;
    height: 1000px;
    background: linear-gradient(180.01deg, #9B8BFF -1.65%, rgba(155, 139, 255, 0.7) 99.99%);
    border-radius: 36px;

    margin : 20px 0 0 20px ;

    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: space-between;

}

.sgnp_header{

    font-family: 'Bungee', cursive;
    font-style: normal;
    font-weight: 400;
    font-size: 50px;
    line-height: 75px;
    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    letter-spacing: 0.025em;
    color: white;

    width: 56%;
    margin-left: 55px;

}

.sec{


    width: 50%;
    text-align: center;
    color: white;

    letter-spacing: 0.025em;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;

    margin-top: -120px;
}

.gdprImg{

    margin-bottom: 30px;
    width: 75%;

}