.chat_cntnr{

    height: 753px;
    width: 100%;

    background: linear-gradient(129.83deg, #71E5FF 20%, rgba(0, 0, 0, 1) 90%);
    /* background-color: chocolate; */

    position: relative;

    margin: 0;
    
}

.chat_cntnr_header{
    display: flex;
    justify-content: space-around;
    padding-top:20px ;
}

.chat_cntnr_header_section{

    padding: 10px 70px;

    background-color: #9B8BFF;
    color: white;

    border-radius: 10px;

    border: 1px solid black;
    
}

.search_msg{

    width: 200px;
    height: 40px;

    background-color: #E6E6E6;

    border: 1px solid black;

    padding-left: 10px;
}

.search_msg::placeholder{

    padding-left: 0px;
}

.chat_footer{
    width: 100%;
    display: flex;
    gap: 25px;

    position: absolute;
    bottom: 20px;
    left: 40px;
}

.chat_msg{

    width: 85%;
    height: 50px;

    border: none;
    border-radius: 30px;

    

    padding-left: 30px;

}

.chat_msg::placeholder{

    padding-left: 0px;
}

.chat_footer_btn{

    width: 50px;
    height: 50px;

    border-radius: 30px;
    border: none;

    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;

}