.footer_container{

    display: flex;
    flex-direction: column;
    justify-content: space-between;

    width: 100%;
    height: 100%;

    margin: 0;

    background-color: azure;

    font-family: 'Arvo';

}

.footer_container hr{
    width: 90%;
    height: 5px;

    background-color: #9B8BFF;

    border-radius: 3px;

    margin-top: 50px;
}

.footer_container section{

    display: flex;
    align-items: center;
    justify-content: space-evenly;

    margin-bottom: 30px;

}

.go_to_top{

    display: flex;
    align-items: center;
    justify-content: space-around;

    width: 10%;

    cursor: pointer;

    text-decoration: none;
    color: black;

}