/*              Footer                */



.categories_footer{

    position: absolute;
    bottom: 0px;

    background: white;

    width: 100%;
    height: 250px;

    display: flex;
    flex-direction: column;

}

.categories_footer_hr{

    width: 90%;
    height: 6px;

    border-radius: 3px;
    border: none;

    background-color: black;

    z-index: 1;
    
}

.categories_footer_div{

    display: flex;
    align-items: center;
    justify-content: space-around;

}

.categories_footer_section1{

    z-index: 1;

}

.categories_footer_h2{

    color: #9B8BFF;

}

.categories_footer_table td{

    padding: 10px;

}

.categories_footer_a{

    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 10px;

    text-decoration: none;
    color: black;

    z-index: 10;

}



