.chats_cntnr{

    width: 100%;
    /* height: auto; */
    height: 753px;
    display: flex;
    justify-content: flex-start;

}

/*               ALL CHATS               */

.chats_aside{

    height: 100%;
    width: 33%;

    margin: 0;

    background-color: #9B8BFF;

    overflow-y: scroll;

    /* order: 1; */

}

.chats_aside_header{

    display: flex;
    align-items: center;
    gap: 120px;
    
    /* margin-bottom: 10px; */

    height: 60px;

}

.chats_ArrowLeft{

    margin-left: 20px;

    cursor: pointer;

}

.chats_functionLogo{

    cursor: text;

}

.chat_with{

    width: 100%;
    height: 110px;

    background-color: #E56E6E;

    display: flex;
    justify-content: space-around;
    align-items: center;

    cursor: pointer;

    border-bottom: 1px solid;

}

.chat_with_img{

    width: 80px;
    height: 80px;

    border-radius: 50%;

}

.chat_with_p{

    color: white;
    font-weight: 500;

    
}

.chat_with_div{

    display: flex;

    background-color: #1700A4;
    color: white;
    font-weight: 500;

    border-radius: 20px;

    padding: 10px;

    font-size: 12px;

}


