.Settings_container{

    margin: 0;
    height: 1500px;
    font-family: 'Arvo';
    background-color: #9B8BFF;
    
    display: flex;
    flex-direction: column;

}


/*   Nav Bar     */

.Settings_navbar{
    
    list-style-type: none;

    display: flex;
    align-items: center;
    justify-content: space-around;

    height: 10%;


}

.Settings_navbar li:last-child{

    display: flex;
    justify-content: space-between;
    align-items: center;

    width: 120px;

    color: white;

    font-weight: 600;
    font-style: italic;

}

.Settings_navbar li:hover{
    cursor: pointer;
}

/*    Main    */

.Settings_main{


    height: 90%;

    background-color: white;

    border-radius: 50px 50px 0 0;
    border-top: 6px solid #F2FF59 ;

    position: relative;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

}

.Settings_main_div{

    width: 80%;
    height: 80%;

    border-radius: 20px;

    z-index: 1;

    background: rgba(255, 255, 255, 0.62);
    box-shadow: 0px 4px 50px 9px rgba(0, 0, 0, 0.25);
    backdrop-filter: blur(25.5px);

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;

}

.Settings_main_div_h2{

    -webkit-text-stroke-color: black;
    -webkit-text-stroke-width: 2px;
    /* color: white; */
    -webkit-text-fill-color: white;
    font-size: 40px;
    font-style: italic;


    display: flex;
    justify-content: flex-start;

    width: 80%;

    margin: 0;

}

.Settings_main_div_p{

    font-weight: 600;
    font-style: italic;

    display: flex;
    justify-content: flex-start;

    width: 70%;

    margin: 0;

}

/*           Internal Section           */


.Settings_main_div_section{

    width: 90%;
    height: 60%;

    /* border: 2px solid; */

    display: flex;

    justify-content: flex-start;
}

.Settings_main_div_section_hr{

    background-color: #9B8BFF;

    width: 0.5%;
    /* height: 550px; */

    margin: 10px;

    border-radius: 10px;

}

/*  NAV  Start  */

.Settings_main_div_section_nav{

    order: 3;

    display: flex;
    flex-direction: column;

    justify-content: space-between;

    align-items: center;

    width: 30%;

}

.Settings_main_div_section_nav_div{

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;

    height: 48%;

}

.Settings_main_div_section_nav_div_img{

    width: 250px;
    height: 250px;

    border-radius: 20px;
    border: 2px solid;

}

.Settings_main_div_section_nav_div_label{

    display: flex;
    justify-content: space-evenly;
    align-items: center;

    width: 100%;

    font-weight: 600;
    font-size: 14px ;

    cursor: pointer;
}

.Settings_main_div_section_nav_btn{

    background: #9B8BFF;

    color: white;

    box-shadow: 0px 0px 12px 3px rgba(0, 0, 0, 0.25);

    width: 120px;
    height: 50px;

    border-radius: 10px;
    border: none;

    font-weight: 700;
    font-style: italic;

    cursor: pointer;

}


/*   NAV End   */

/*   INPUTS  START   */

.Settings_main_div_section_div{

    order: 2;

    width: 65%;

    display: flex;
    flex-direction: column;
    justify-content: space-evenly;

    font-style: italic;
    font-weight: 700;

}

.MessageEdit{
    cursor: pointer;
}


