.wallet_container{

    margin: 0px;

    width: 100%;
    height: 2500px;

    background-color: #9B8BFF;
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.wallet_navbar{

    width: 100%;
    height: 8%;
    list-style-type: none;

    /* display: list-item; */
    display: flex;
    width: 90%;
    justify-content: space-around;
    align-items: center;

    color: white;

}

/* .wallet_navbar li{
    
} */

.wallet_navbar li:last-child{
    width: 120px;
    display: flex;
    align-items: center;
    justify-content: space-between;

    
    font-family: 'Arvo';
    font-style: italic;
    font-weight: 600;
}

.wallet_navbar li:hover{
    cursor: pointer;
    
}

.wallet_main{

    width: 100%;
    height: 100%;

    border-radius: 40px 40px 0 0;
    background-color: white;
    border-top: 6px #F2FF59 solid;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;

    position: relative;

}

