.login_page {

    display: flex; 
    justify-content: space-between;
    margin-top: 20px;

}

*{
    font-weight: 600;
    font-family: 'Arvo', serif;
}

.func_logo{

    order: 3;
    width: 189px;
    height: 55px;
}

.welcomBox{

    order: 1;
    
    width: 30%;
    height: 720px;
    background: linear-gradient(179.63deg, #9B8BFF 0.29%, rgba(155, 139, 255, 0.7) 99.66%);
    border-radius: 36px;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    margin-left: 50px;
    

}

.login_information{

    width: 60%;
    order: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 80px;
    justify-content: space-around;
    z-index: 1;
}

.welcomBox_welcome{

    
    font-family: 'Arvo';
    font-style: italic;
    font-size: 32px;
    font-weight: 700;
    line-height: 40px;
    display: flex;

    width: 65%;
}

.welcomBox_happy{

    margin-top: -50px;

    color: white;
    font-size: 24px;
    font-family: 'Arvo';
    font-weight: 700;
    font-style: italic;
    display: flex;

    width: 80%;
}

.welcomBox_img{

    width: 400px;
    height: 300px;
}

.loginBox{

    
    width: 60%;
    height: 500px;
    border: black solid 4px;
    border-radius: 20px;

    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.login_btn{

    
    width: 40%;
    height: 51px;
    background: linear-gradient(90deg, #BC63FF -3.47%, rgba(188, 99, 255, 0.64) 103.32%);
    border-radius: 12px;
    color: white;
    cursor: pointer;
    font-size: 16px;
    border: white;

    letter-spacing: 0.025em;
    font-style: normal;
    /* font-weight: 700; */

    display: flex;
    align-items: center;
    /* justify-content: center; */

}

.login_btn:hover{

    background: linear-gradient(90deg,rgba(188, 99, 255, 0.64) -3.47%, #BC63FF  103.32%);

}

.loginBox input[type='email'] ,.loginBox input[type='password']{

    width: 300px;
    height: 40px;
    border-radius: 10px;
}

.email_style::placeholder , .password_style::placeholder{

    font-size: 10px;
    font-family: 'Arvo';
    font-style: italic;
    font-weight: 600;
    
}

.email_style::placeholder{
    padding-left: 30px;
}

.password_style::placeholder{
    padding-left: 30px;
}

.login_sentence{

    font-family: 'Bungee', cursive;
    color: #9B8BFF;

    text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    letter-spacing: 0.025em;

    font-style: normal;
    font-weight: 400;
    font-size: 64px;
    line-height: 77px;
}

.login_information_div2{

    font-family: 'Arvo';
    font-style: italic;
    font-weight: 700;
    font-size: 14px;
    line-height: 25px;
}



.loginBox_div1{

    margin: 30px 0 0 65px;
    width: 73%;
    font-style: italic;
    font-weight: 700;
    font-size: 16px;
    
    
}


.loginBox_div2{

    margin-left: 65px;
    width: 73%;
    font-style: italic;
    font-weight: 700;
    font-size: 16px;
}

.loginBox_div3{

    display: flex;
    justify-content: space-around;
    
    letter-spacing: 0.025em;
    font-family: 'Arvo';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 22px;

    width: 100%;
    
}

.loginBox_div3_section{

    display: flex;
    justify-content: center;
    align-items: center;

    width: 50%;
    padding-left: 40px;

    font-style: normal;
    font-weight: 700;
    /* font-size: 18px; */
    line-height: 22px;
    
}


/* input[type='checkbox']{
    width: 40px;
}
.loginBox_div3_input{

    width: 40px;
} */

.loginBox_div3 a{

    /* padding-left: 10px; */
    font-style: normal;
    font-weight: 700;
    /* font-size: 18px; */
    line-height: 22px;
    width: 50%;
    color: #9B8BFF;
    text-align: center;
    padding-top: 10px;
    
    
}

.loginBox_div4{
    display: flex;
    justify-content: center;
    width: 100%;
}

.loginBox_div5{

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    font-style: italic;
    font-weight: 700;
    font-size: 17px;
}

.signup_link{

    font-family: 'Arvo';
    font-style: italic;
    font-weight: 700;
    font-size: 20px;
    line-height: 32px;

    filter: drop-shadow(0px 6px 4px rgba(0, 0, 0, 0.25));
    color: #BE68FF;
}


.person_lock{

    position: absolute;
    top: 55px;
    left: 480px;
    width: 18%;
    z-index: 0;
}